.become {
    margin: 0;
    padding: 0;
    width: 100%;
}

.become__title-span {
    margin: 0;
    padding: 0;
    width: 100%;
    min-height: 57px;
    font-style: normal;
    font-weight: 700;
    font-size: 52px;
    line-height: 129%;
}

.become__list-item {
    margin: 0;
    padding: 21px 20px 0;
    background: #F2F2F2;
    max-width: 580px;
    border-radius: 10px;
    min-height: 521px;
    z-index: 1;
}

.become__item {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.become__name {
    margin: 0;
    padding: 0;
    width: 480px;
    height: 45px;
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 45px;
}

.become__circle {
    margin: 0;
    padding: 0;
    background: #F4D011;
    box-shadow: 0px 3px 4px rgba(153, 153, 153, 0.25), 0px -3px 4px rgba(153, 153, 153, 0.1);
    clip-path: circle(50%);
    height: 60px;
    width: 60px;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 156%;
    text-align: center;
    color: #171717;
    display: flex;
    align-items: center;
    justify-content: center;
}

.become__kouch {
    display: flex;
    margin: 0;
    padding: 0;
}

.become__text {
    margin: 10px 0 0 12px;
    padding: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 156%;
    max-width: 360px;
}

.become__block {
    margin: 10px 0;
    padding: 0;
}

.become__block-list {
    margin: 0 0 9px 0;
    padding: 0;
    display: flex;
    align-items: baseline;
}

.become__start {
    margin: 0 10px 0 0;
    padding: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 156%;
}

.become__plan {
    margin: 18px 0 0;
    padding: 0;
    width: 533px;
    height: 66px;
    background: #FFFFFF;
    box-shadow: 0px 3px 4px rgba(153, 153, 153, 0.25), 0px -3px 4px rgba(153, 153, 153, 0.1);
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
}

.become__left {
    margin: 11px 0 0 20px;
    padding: 0;
}

.become__months {
    display: flex;
    align-items: center;
}

.become__months-block {
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
}

.become__price {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 136%;
}

.become__price-number {
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
}

.become__right {
    margin: 0 20px 0 0;
    padding: 0;
    display: flex;
    gap: 21px;
    text-align: center;
    align-items: center;
}

.become__money {
    margin: 0;
    padding: 0;
    display: flex;
}

.become__money-block {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
}

.become__monthly {
    margin: 0;
    padding: 0;
    display: flex;
}

.become__monthly-block {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 30px;
    text-align: right;
}

.become__prices {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 156%;
}

.become__buttons {
    margin: 20px 0 0 0;
    padding: 0;
    display: flex;
    gap: 20px;
}

.become__button-detailed {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 80px;
    width: 260px;
    height: 65px;
    background: #FFFFFF;
    /* border: 2px solid #252525; */
    box-shadow: 0px 4px 4px rgba(85, 85, 85, 0.25);
    border-radius: 5px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    color: #252525;
}

.become__button-signup {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 80px;
    width: 260px;
    height: 65px;
    background: #F4D011;
    box-shadow: 0px 4px 4px rgba(85, 85, 85, 0.25);
    border-radius: 5px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    color: #171717;
    border: none;
}